.button {
    display: block;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    background-image: url(/static/media/ButtonMain.09b4cfbb.svg);
    width: 90%;
    max-width: 500px;
    margin: auto;
    background-position: center;
    background-repeat: no-repeat;
    font-size: 18px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 1.4px;
    cursor: pointer;
    color: #577158;
    transition: all 0.2s, transform 0.1s;
}

.button:hover, .circle-button:hover {
    opacity: 0.6;
}

.button:active, .circle-button:active {
    opacity: 0.8;
    transform: scale(0.9);
}

.button-dark {
