.menu {
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;
    position: fixed;
    background-color: rgba(255, 255, 255, 0.6);
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 5%;
    overflow-y: scroll;
}

.menu.open {
    opacity: 1;
    visibility: visible;
}

.close-menu-button {
    width: 40px;
    height: 40px;
    cursor: pointer;
    padding: 12px;
    border-radius: 50%;
    transition: all 0.2s, transform 0.1s;
}

.close-menu-button:hover {
    opacity: 0.8;
    background-color: rgba(0, 0, 0, 0.07);
}

.close-menu-button:active {
    opacity: 1;
    transform: scale(0.8);
    background-color: rgba(0, 0, 0, 0.1);
}

.menu-links {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 80%;
}

.menu.open .menu-links {
    -webkit-animation: menu-links 0.4s cubic-bezier(0.25, 0.75, 0, 1);
            animation: menu-links 0.4s cubic-bezier(0.25, 0.75, 0, 1);
}

@-webkit-keyframes menu-links {
    from {
        opacity: 0;
        transform: translateY(400px);
    }
    to {
        transform: none;
    }
}

@keyframes menu-links {
    from {
        opacity: 0;
        transform: translateY(400px);
    }
    to {
        transform: none;
    }
}

.menu-links a {
    max-width: 500px;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    padding: 20px;
    display: block;
    cursor: pointer;
    border-radius: 16px;
    transition: all 0.25s, transform 0.1s;
    font-size: 22px;
    font-weight: 600;
}

.menu-links a:hover {
    background-color: rgba(0, 0, 0, 0.07);
}

.menu-links a:active {
    opacity: 1;
    transform: scale(0.8);
    background-color: rgba(0, 0, 0, 0.12);
}
