.button {
    display: block;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    background-image: url('../images/ButtonMain.svg');
    width: 90%;
    max-width: 500px;
    margin: auto;
    background-position: center;
    background-repeat: no-repeat;
    font-size: 18px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 1.4px;
    cursor: pointer;
    color: #577158;
    transition: all 0.2s, transform 0.1s;
}

.button:hover, .circle-button:hover {
    opacity: 0.6;
}

.button:active, .circle-button:active {
    opacity: 0.8;
    transform: scale(0.9);
}

.button-dark {
    display: block;
    text-align: center;
    padding: 12px;
    box-sizing: border-box;
    background-image: url('../images/button_designs_dark.svg');
    width: 90%;
    max-width: 500px;
    margin: auto;
    background-position: center;
    background-repeat: no-repeat;
    font-size: 18px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 1.4px;
    cursor: pointer;
    color: #577158;
    transition: all 0.2s, transform 0.1s;
}

.button-dark:hover, .circle-button:hover {
    opacity: 0.6;
}

.button-dark:active, .circle-button:active {
    opacity: 0.8;
    transform: scale(0.9);
}

.circle-button {
    width: 40vw;
    height: 40vw;
    max-width: 260px;
    max-height: 260px;
    background-image: url('../images/ButtonCircle.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-size: 40px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 1.4px;
    cursor: pointer;
    color: #7e8b7f;
    transition: all 0.2s, transform 0.1s;
}

.circle-button subtitle {
    font-size: 24px;
    letter-spacing: 5px;
    font-weight: 600;
    color: #577158;
}