@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&display=swap');

.green-border {
    display: block;
    text-align: center;
    box-sizing: border-box;
    padding: 150px 50px;
    background-image: url('../images/green_border.svg');
    width: 90%;
    height: 500px;
    max-width: 500px;
    margin: auto;
    background-position: center;
    background-repeat: no-repeat;
    font-size: 35px;
    font-weight: 800;
    font-family: 'Inconsolata', monospace;
    letter-spacing: 1.4px;
    cursor: pointer;
    color: #1c1c1c;
    transition: all 0.2s, transform 0.1s;
}

.green-border:hover {
    opacity: 0.6;
}

.green-border:active {
    opacity: 0.8;
    transform: scale(0.9);
}